<template>
    <div>
        <h1>导航栏</h1>
        <!-- <nav-bar>  vue框架中不区分大小写    -->
        <NavBar title="叶子主页" @click-back="backFn" />
        <h1>swiper轮播图</h1>
        <!-- swiper轮播图，
        1 安装模块 swiper、vue-awesome-swiper
        2 局部注册 导入模块Swiper 5.x；加入components、directives
        3 复制 swiper组件代码 -->
        <template>
        <swiper ref="mySwiper" :options="swiperOptions" class="swiper">
            <swiper-slide>Slide 1</swiper-slide>
            <swiper-slide>Slide 2</swiper-slide>
            <swiper-slide>Slide 3</swiper-slide>
            <swiper-slide>Slide 4</swiper-slide>
            <swiper-slide>Slide 5</swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
        </template>

        <hr/>
        <!-- 待完成列表 -->
         <h1>TODO List</h1>
        <!-- input中先保存模型数据 ，再 button 添加点击事件 -->
        <input type="text" v-model="title">
        <button @click="todoFn">添加</button>
        <ul>
            <!-- 在框架里面遍历，必须写v-bind：key="唯一标识"， 原因 涉及到虚拟DOM 、diff算法 -->
            <li v-for="(item,index) in todos" v-bind:key="index">
                {{item.title}} <button @click="delFn(index)">删除</button>
            </li>
        </ul>
    </div>
</template>

<script>

import { Swiper, SwiperSlide, directive } from 'vue-awesome-swiper'

// import style (>= Swiper 6.x)
// import 'swiper/swiper-bundle.css'

// import style (<= Swiper 5.x)
import 'swiper/css/swiper.css'
// import NavBar from '../components/NavBar.vue';

export default {
    computed: {
      swiper() {
        return this.$refs.mySwiper.$swiper
      }
    },
    mounted() {
      console.log('Current Swiper instance object', this.swiper)
      this.swiper.slideTo(3, 1000, false)

      // 进行异步请求， 让它出现跨域问题，再解决跨域
      // fetch('http://kg.zhaodashen.cn/v1/cart/index.jsp')
      //方法一
      fetch('/api/v1/cart/index.jsp')          // /api开头 对应 vue.config.js中的键
      .then(res=>res.json())
      .then(res=>{
          console.log(res);
      })
     //方法二
     fetch('/v1/cart/index.jsp')               // /v1开头  对应 vue.config.js中的键
      .then(res=>res.json())
      .then(res=>{
          console.log(res);
      })

    },
     // 局部注册
    components: {
        Swiper,
        SwiperSlide,
        // 局部注册
        // NavBar,
    },
 
    directives: {
        swiper: directive
    },


    data(){

        return {

             swiperOptions: {
                pagination: {
                    el: '.swiper-pagination'
                },
                // Some Swiper option/callback...
            },

            title:'',
            todos:[
                {id:1,title:'吃饭'},
                {id:2,title:'睡觉'},
                {id:3,title:'大豆'}
                ]
        }
    },
    methods:{
        backFn(){
           alert('返回')
        },
        delFn(index){                       
            this.todos.splice(index,1)
        },
        todoFn(){
            this.todos.push({
                id:this.todos.length+1,
                title:this.title,
            });
            this.title=''

        },
    }

};
</script>

<style scoped>
    .swiper{
        width: 600px;
        height: 300px;
        color:seagreen;
        background-color: yellow;
    }
</style>
